import React from 'react'
import styled from '@emotion/styled'
import { Carousel } from 'react-bootstrap'
import AGDUP from "../images/Advanced Game Development Using PyGame.png"
import { Link } from "gatsby"


const Heading = styled.h1`
font-size: 60px;
padding-top: 60px;
padding-bottom: 25px;`


const CourseCarousel = () => (
    <div>
    <center>
    <Heading> Computer Science Courses</Heading>
    <Carousel>
        <Carousel.Item>
                    <Link to="https://www.coursera.org/account/accomplishments/verify/2STKTRTG6BQ5">
                        <img src={AGDUP} alt="Advanced Game Development Using PyGame" />
                    </Link>
                    <Carousel.Caption>Advanced Game Development Using PyGame</Carousel.Caption>
        </Carousel.Item>
    </Carousel>
    </center>
    </div>

)

export default CourseCarousel

